.footer {
  @include page-grid;

  position: relative;
  grid-template-rows: repeat(2, auto);
  justify-items: center;

  @include on-tablet {
    grid-template-rows: repeat(3, auto);
  }

  &__logo {
    display: none;

    @include hover(transform, scale(1.2));

    @include on-tablet {
      display: block;
      padding-bottom: 60px;
      grid-area: 1 / 1 / 2 / -1;
    }
  }

  &__up-link {
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    display: none;
    justify-content: center;
    align-items: center;

    @include hover(transform, scale(1.2));

    @include on-tablet {
      display: flex;
    }

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__social-container {
    grid-area: 1 / 1 / 2 / -1;
    width: 152px;
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;

    @include on-tablet {
      grid-area: 2 / 1 / 3 / -1;
      width: 170px;
      padding-bottom: 60px;
    }
  }

  &__social-link {
    @include hover(transform, scale(1.2));
  }

  &__copyright {
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    text-align: center;
    color: $c-gray2;
    grid-area: 2 / 1 / 3 / -1;

    @include on-tablet {
      font-weight: 300;
      text-align: center;
      grid-area: 3 / 1 / 4 / -1;
    }
  }
}
