.form {
  width: 100%;
  height: 100%;

  @include page-grid;

  grid-template-rows: repeat(4, auto);

  @include on-tablet {
    grid-template-rows: repeat(3, auto);
  }

  &__title {
    grid-area: 1 / 1 / 2 / -1;
    padding-bottom: 27px;

    @include on-tablet {
      grid-area: 1 / 1 / -1 / 5;
    }

    @include on-desktop {
      grid-area: 1 / 1 / -1 / 7;
    }
  }

  &__input {
    border: 1px solid #828282;
    padding-inline: 20px;
    background-color: $c-gray;

    @extend %p;

    &::placeholder {
      color: $c-gray1;
    }

    &:focus-visible {
      outline: none;
      border-color: #0c797a;
    }

    &--email {
      grid-area: 2 / 1 / 3 / -1;
      height: 48px;
      margin-bottom: 20px;

      @include on-tablet {
        grid-area: 1 / 5 / 2 / -1;
      }

      @include on-desktop {
        grid-area: 1 / 7 / 2 / -1;
      }
    }

    &--message {
      grid-area: 3 / 1 / 4 / -1;
      height: 128px;
      resize: none;
      margin-bottom: 20px;

      @include on-tablet {
        height: 201px;
        grid-area: 2 / 5 / 3 / -1;
        margin-bottom: 30px;
      }

      @include on-desktop {
        height: 128px;
        grid-area: 2 / 7 / 3 / -1;
      }
    }
  }

  &__button {
    grid-area: 4 / 1 / 5 / -1;
    width: 102px;
    height: 40px;

    @include on-tablet {
      grid-area: 3 / 5 / 4 / -1;
    }

    @include on-desktop {
      width: 124px;
      height: 48px;
      grid-area: 3 / 7 / 4 / -1;
    }
  }
}
